<template>
  <div>
    <div @click="post">
      首页
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    post() {
      this.$http.home
        .testPost({
          aaa: 11
        })
        .then(res => {
          console.log(res);
        });
    },
    get() {
      this.$http.home
        .testGet({
          aaa: 11
        })
        .then(res => {
          console.log(res);
        });
    }
  },
  created() { }
};
</script>

<style scoped lang="less"></style>



