<template>
  <div>
    我的
    <div class="logoutBtn">
      <van-button type="danger"
        @click="logOut"
        block>退出登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {

    };
  },
  methods: {
    logOut() {
      this.$http.home
        .testGet({
          aaa: 11
        })
        .then(res => {
          localStorage.removeItem('userInfo')
          this.$store.commit('removeToken', '')
          this.$router.push('/home')
        });
    }
  },
  created() {

  },
};
</script>

<style scoped lang="less">
.logoutBtn {
  position: fixed;
  bottom: 90px;
  left: 15px;
  right: 15px;
}
</style>



